<template lang="html">
<div class="page">
  <cube-scroll ref="scroll" :options="{scrollbar:{fade:true}}">
    <div class="title-bar">
      <h2>{{obj.Title}}</h2>
      <p>{{obj.Summary}}</p>
    </div>
    <div class="flex bg-white line-top p-md">
      <avatar :src="obj.Avatar" class="mr"></avatar>
      <div class="f-body">
        <div class="fs-sm tc-grey">{{obj.NickName}}</div>
        <div class="desc flex jc-sb">
          <p class="left">部门：财务部</p>
          <span>{{ obj.UpdateTime }}</span>
        </div>
      </div>
    </div>
    <tit has-bg class="mt-md">填写信息</tit>
    <div class="p-md bg-white">
      <cube-textarea v-model="content" placeholder="输入您要处理的内容" :maxlength="50"></cube-textarea>
      <div class="select-group mt-md">
        <div class="label">选择办理人员</div>
        <div class="select-tap">
          <div class="select-item">第一个</div>
          <div class="select-item">第二个</div>
        </div>
      </div>
    </div>
    <div class="btn-wrap">
      <btn type="primary" size="lg" block>转移办理</btn>

    </div>

  </cube-scroll>

</div>
</template>

<script>
export default {

  data() {
    return {
      List: [],
      obj:{},
      content:''
    }
  },
  created () {
    this.getListData()
  },
  methods : {
    getListData() {
      this.id = this.$route.params.id
      this.axios.get('/static/data.json').then((res) => {
        this.List = res.data.List
        this.obj = res.data.List[this.id]
      })
    },


  },
}
</script>

<style lang="stylus">
@import "assets/stylus/base"

</style>



